<!-- Tip 拆箱弹窗 -->
<template>
  <el-dialog
    :title="title"
    top="30vh"
    :visible="value"
    :show-close="true"
    width="920px"
    :lock-scroll="false"
    :destroy-on-close="true"
    :append-to-body="true"
    :close-on-click-modal="false"
    :close-on-press-escape="false"
    :before-close="beforeClose"
    @open="initSelect"
  >
    <el-scrollbar class="scroll-dialog">
      <el-descriptions>
        <el-descriptions-item label="物料编码">
          {{ extraInfo.materialCode || '---' }}
        </el-descriptions-item>
        <el-descriptions-item label="物料名称">
          {{ extraInfo.materialName || '---' }}
        </el-descriptions-item>
        <el-descriptions-item label="规格型号">
          {{ extraInfo.materialSpec || '---' }}
        </el-descriptions-item>
        <el-descriptions-item label="数量">
          {{ extraInfo.num || '---' }}
        </el-descriptions-item>
        <el-descriptions-item label="spq">
          {{ extraInfo.materialSpq || '---' }}
        </el-descriptions-item>
        <el-descriptions-item label="计量单位">
          {{ extraInfo.unitName || '---' }}
        </el-descriptions-item>
        <el-descriptions-item label="位置">
          {{ extraInfo.currentPosition || '---' }}
        </el-descriptions-item>
        <el-descriptions-item label="入库日期">
          {{ extraInfo.inWarehouseDate || '---' }}
        </el-descriptions-item>
        <el-descriptions-item label="在库时长">
          {{ extraInfo.inWarehouseDays || '---' }}
        </el-descriptions-item>
        <el-descriptions-item label="条码号">
          {{ extraInfo.barcodeNumber || '---' }}
        </el-descriptions-item>
      </el-descriptions>
      <el-divider />
      <template v-if="!showDetail">
        <el-form :ref="refName" :model="form" inline>
          <el-form-item prop="splitNum" label="按最小sku拆出数量">
            <el-input-number v-model="form.splitNum" :min="0" :precision="3" />
            <!-- <el-input-number v-model="form.splitNum" :min="1" :max="Math.floor(extraInfo.num / extraInfo.materialSpq)" label="请填写" /> -->
          </el-form-item>
        </el-form>
      </template>
      <template v-else>
        <el-descriptions>
          <el-descriptions-item label="按最小sku拆出数量">
            {{ extraInfo.materialCode || '---' }}
          </el-descriptions-item>
        </el-descriptions>
        <table-com
          ref="baseTableRef"
          :refresh-key="refreshKey"
          :table-height-type="150"
          :loading="loading"
          :index="true"
          :index-fixed="false"
          :selection="false"
          :multiple="false"
          :selection-fixed="false"
          :table-column-sort="false"
          :table-data="barCodeList || []"
          :column-list="columnList"
          :show-operate="showOperate"
          :operate-fixed="false"
          :table-selection="tableSelection"
          :operation-width="100"
          @selectionChange="selectionChange"
        />
      </template>
    </el-scrollbar>
    <div slot="footer">
      <el-button v-if="!showDetail" type="primary" @click="confirmDialog">确认拆出并生成条码</el-button>
      <el-button v-else type="primary" @click="confirmAndPrint">确定并打印</el-button>
      <el-button @click="beforeClose">取 消</el-button>
    </div>
  </el-dialog>
</template>

<script>
import paginationPage from '@/mixins/paginationPage'
import printTagWired from '@/mixins/printTagWired'
import { requestWrapper } from '@/utils/utils'
import { splitNum1Api } from '@/apis/modules/warehouse/inventory'

const INIT_FORM = () => ({
  splitNum: 0
})

export default {
  name: 'DevanningDialog',
  mixins: [paginationPage, printTagWired],
  props: {
    value: {
      required: true,
      type: Boolean
    },
    title: {
      type: String,
      default: '拆箱'
    },
    extraInfo: {
      type: Object,
      required: true,
      default: () => {}
    }
  },
  data() {
    return {
      refName: 'DevanningForm',
      form: INIT_FORM(),
      showDetail: false, // 是否展示拆箱详情
      refreshKey: 1, // 表格唯一key
      showOperate: false, // 是否展示表格操作栏
      loading: false, // 表格loading
      tableSelection: [], // 表格选中数据
      columnList: [
        {
          module: 'word',
          prop: 'barcodeNumber',
          label: '条码号',
          align: 'center',
          minWidth: 170,
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'num',
          label: '数量',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'unitName',
          label: '计量单位',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'minSkuUnitName',
          label: '最小包装单位',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'currentPosition',
          label: '位置',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        }
      ],
      barCodeList: [] // 拆箱后的条码
    }
  },
  dicts: [],
  methods: {
    // Tip 确定并打印
    confirmAndPrint() {
      this.$emit('confirm', this.extraInfo)
      this.beforeClose()
    },
    // Tip 确认拆出并生成条码
    async confirmDialog() {
      const params = {
        ...this.form,
        id: this.extraInfo.inventoryBookDetailId
      }
      const res = await requestWrapper(splitNum1Api(params))
      if (!res.success) return
      // 更新条码列表
      this.$emit('confirm', this.extraInfo)
      this.$message({
        message: `操作成功`,
        type: 'success'
      })
      this.barCodeList = res.data || [] // 拆箱后的条码列表
      this.showDetail = true // 展示拆箱详情
    },
    // Tip 表格复选框改变回调
    selectionChange(tableSelection) {
      this.tableSelection = [...tableSelection]
    },
    initSelect() {
      // this.form.splitNum = Math.floor(this.extraInfo.num / this.extraInfo.materialSpq)
    },
    beforeClose(done) {
      this.$emit('input', false)
      this.form = INIT_FORM()
      this.showDetail = false // 恢复默认不展示拆箱详情状态
      typeof done === 'function' && done()
    }
  }
}
</script>

<style lang="scss" scoped>
.scroll-dialog {
  height: 350px;
}
::v-deep .el-scrollbar .el-scrollbar__wrap {
  overflow-x: hidden;
}
::v-deep .el-dialog__body {
  padding-top: 10px;
  padding-bottom: 0;
}
</style>

